/* Desktop styles */
.navbar {
  height: 5rem;
  width: auto;
  /* min-width: 75rem; */
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  z-index: 3;
  top: 0;
  background-color: rgb(37, 36, 36);
}

.navbarResumeBtn {
  background-color:rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  margin: 1rem;
  padding: 5px;
  border: none;
  border-radius: 0.5rem;
  font-weight: bold;
  transition: color .3s ease-in-out,
  box-shadow .3s ease-in-out;
  font-size: large;
  cursor: pointer;
  /* border: 2px dashed rgb(255, 225, 0); */

}

.navbarResumeBtn:hover {
  color: #000000;
  box-shadow: inset 200px 0 0 0 #c0c06a;
  border: #fff;
}

.desktopMenuListItem {
  margin: 1rem;
  cursor: pointer;
}

.desktopMenuListItem:hover {
  color: yellow;
  padding-bottom: 0.5rem;
  border-bottom: 3px solid yellow;
}

.desktopMenuBtn {
  background: white;
  color: black;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  border-radius: 2rem;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.desktopMenuBtn:hover {
  color: #fff;
  box-shadow: inset 200px 0 0 0 #b55454;
  border: #fff;
}

.active {
  color: yellow;
  padding-bottom: 0.5rem;
  border-bottom: 3px solid yellow;
}
.mobMenu{
  display: none;
}
.logo{
  width: 14rem;
}

/* Tablet styles */
@media (max-width: 992px) {
  .navbar {
    padding: 1rem;
    justify-content: space-between;
    height: 4rem;
  }

  .logo {
    width: 12rem;
  }
  .navbarResumeBtn{
    display: none;
  }

  .desktopMenu {
    display: none;
  }

  .mobMenu {
    display: block;
    cursor: pointer;
    height: 3rem;
    width: 3rem;
  }

  .navMenu {
    position: absolute;
    top: 4rem;
    right: 1rem;
    z-index: 2;
    display: none;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 10rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
  }

  .listItem {
    color: rgb(255, 255, 255);
    padding: 0.5rem 0.5rem;
    margin: 0.25rem;
    background: rgb(67, 65, 65);
    cursor: pointer;
  }
  .listItem:hover{
    background-color: rgb(94, 89, 83);
  }
}

/* Mobile styles */
@media (max-width: 768px) {
  .logo {
    width: 10rem;
  }
}

/* Extra Small Mobile styles */
@media (max-width: 576px) {
  .logo {
    width: 9rem;
  }
  .navbar{
    height: 3.5rem;
    padding: 0.5rem;
  }
  .mobMenu{
    height: 2rem;
    width: 2.5rem;
  }
  .navMenu {
    padding: 0.5rem;
    min-width: 7rem;
    border-radius: 1rem;
    /* width: 3rem; */
  }

  .listItem {
    padding: 0.5rem 0.5rem;
    margin: 0.25rem;
  }
}



  