.skillHead {
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 4rem;
  font-weight: bold;
  font-size: xx-large;
  color: #c1c110;
}

.skillBar {
  display: flex;
  margin: 1rem;
  background-color: rgb(34, 32, 5);
  padding: 0.5rem;
  border-radius: 0.2rem;
  padding-left: 1rem;
  box-shadow: rgb(218, 218, 230) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;

}

.skillMain {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 0 2rem 2rem;
  margin-bottom: 2rem;
}

.skillImg {
  height: 8vh;
  width: 4vw;
  border-radius: 0.5rem;
  margin-top: 5px;
}

@media (max-width:880px) {
  .skillMain {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    
  }

  .skillImg {
    height: 8vh;
    width: 6vw;

  }
}

@media (max-width:616px) {
  .skillMain {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    
  }

  .skillImg {
    height: 8vh;
    width: vw;

  }
}

@media (max-width:450px) {
  .skillImg {
    height: 8vh;
    width: 10vw;

  }
}