h1 {
    text-align: center;
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.toolBar {
    display: flex;
    margin: 1rem;
    background-color: rgb(34, 32, 5);
    padding: 0.5rem;
    border-radius: 0.2rem;
    /* margin-left: 3rem; */
    padding-left: 1rem;
    box-shadow: 0 0 10px 0 rgb(176, 172, 172) inset;


}

.toolMain {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 0 2rem 2rem;
    margin-bottom: 2rem;
}

.toolImg {
    height: 8vh;
    width: 4vw;
    border-radius: 0.5rem;
    margin-top: 5px;
}

@media (max-width:1170px) {
    .toolMain {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .toolImg {
        height: 10vh;
        width: 6vw;
    }
    .toolText h2{
        font-size: 40px;
    }

}

@media (max-width:850px) {
    .toolMain {
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }

    .toolImg {
        height: auto;
        width: 4rem;
    }
    .toolText h2{
        font-size: 30px;
    }
    
}

