h1 {
    font-size: xx-large;
    color: #c1c110;
    text-align: center;
    /* margin-bottom: 6rem; */
}

.projectMain {
    display: grid;
    justify-content: space-around;
    grid-template-columns: repeat(2, 1fr);
    padding: 2rem;
    margin-left: 3rem;
    margin-bottom: 2rem;
}

.projectItem {
    background-color: rgb(30, 32, 32);
    height: 35rem;
    width: 40vw;
    border: 3px solid rgb(224, 232, 127);
    border-radius: 0.5rem;
    margin-bottom: 3rem;
    /* margin: 0%; */
    box-shadow: rgb(0, 0, 0) 0px 5px 15px;


}

