.projectBtn{
    margin: auto;
    /* padding: 0 40px 20px 10px; */
    display: flex;
    justify-content: space-evenly;
}

.btnweb {
    /* margin: 1rem; */
    padding: 10px;
    /* margin:auto; */
    font-size: large;
    color: rgb(255, 255, 255);
    border-radius: 5px;
    background-color: #000000;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border: none;
    outline: none;
}

.btngit {
    /* margin: 1rem; */
    padding: 10px;
    /* margin-left: 3rem; */
    font-size: large;
    color: rgb(255, 255, 255);
    border-radius: 5px;
    background-color: #000000;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border: none;
    outline: none;
}

.btngit:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left: -2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.btngit:active {
    color: #000
}

.btngit:active:after {
    background: transparent;
}

.btngit:hover:before {
    opacity: 1;
}

.btngit:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000000;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 400% 0;
    }

    100% {
        background-position: 0 0;
    }
}

