#intro {
    /* height: calc(100vh - 4rem); */
    width: 85%;
    height: auto;
    margin: 50px auto;
    padding: 1rem;
    /* background-color: antiquewhite; */
}
.introContent{
    width: 100%;
    margin-top: auto;
    margin-bottom: auto;
    display: flex;
    justify-content: space-between;
    /* background-color: #b5545470; */
}
.introTextContentDiv{
    width: 50%;
    /* display: flex; */
    margin: auto 0px;
    justify-content: center;
    align-items: center;
    /* background-color: chocolate; */

}
.introProfileImgDiv{
    width: auto;
    /* background-color: cornflowerblue; */
}
.photo {
    width:auto;
    height: 450px;
    border-radius: 50%;
    border: 3px solid wheat;
    box-shadow: rgb(0, 0, 0) 0px 30px 90px;   
}

