.hello {
    font-size: 1.75rem;
    font-weight: 100;
}
.helloimg {
    height: 50px;
    width: 50px;
}
.introText{
    font-size: 3rem;
    font-weight: bold;
}
.introName {
    color: yellow;
    /* font-size: xx-large; */
}

.introPara {
    font-size: 1.2rem;
    font-weight: 200;
    /* letter-spacing: 1px; */

}


.btnImg {
    height: 1.2rem;
    object-fit: cover;
    margin: 0.1 0.25rem;
    width: 2rem;

}

.introResumeBtn {
    background-color:rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    margin: 1rem;
    padding: 5px;
    border: none;
    border-radius: 0.5rem;
    font-weight: bold;
    transition: color .3s ease-in-out,
    box-shadow .3s ease-in-out;
    font-size: large;
    cursor: pointer;
    /* border: 2px dashed rgb(255, 225, 0); */

}

.introResumeBtn:hover {
    color: #000000;
    box-shadow: inset 200px 0 0 0 #b5a354;
    border: #fff;
}



@media screen and (min-width:768px) and (max-width:1000px) {

    #intro {
        /* height: calc(100vh - 4rem); */
        margin: 100px auto;
        width: 95%;
        /* background-color: antiquewhite; */
    }
    .photo {
        width: 300px;
        height: 350px;
    }
}

@media screen  and (min-width:410px) and (max-width:768px){
    #intro {
        /* height: calc(100vh - 4rem); */
        margin: 10px auto;
        width: 95%;
        /* background-color: antiquewhite; */
    }
    .photo {
        width: 250px;
        height: 300px;
    }
    .introContent{
        
        flex-direction: column-reverse;
        row-gap: 20px;
      
    }
    .introProfileImgDiv{
        width: auto;
        /* background-color: #b5a354; */
        width: 250px;
        margin: 0px auto;
    }
    .introTextContentDiv{
        width: 100%;
        text-align: center;
       
    
    }
    .introText{
        font-size: 1.2rem;
        font-weight: bold;
    }
   

}

@media screen  and (max-width:410px){
    #intro {
        /* height: calc(100vh - 4rem); */
        margin: 10px auto;
        width: 95%;
        /* background-color: antiquewhite; */
    }
    .photo {
        width: 200px;
        height: 250px;
    }
    .introContent{
        
        flex-direction: column-reverse;
        row-gap: 20px;
      
    }
    .introProfileImgDiv{
        width: auto;
        /* background-color: #b5a354; */
        width: 200px;
        margin: 0px auto;
    }
    .introTextContentDiv{
        width: 100%;
        text-align: center;
       
    
    }
    .introText{
        font-size: 1rem;
        font-weight: bold;
        /* background-color: aqua; */
    }
    .hello {
        font-size: 1rem;
        font-weight: 100;
    }
    .helloimg {
        height: 40px;
        width: 40px;
    }
    .introPara {
        font-size: 1rem;
        font-weight: 100;
        
    
    }
     
}