#about {

    width: 100%;
    height: auto;
    padding: 1rem;
}

.aboutMainDiv{
    width: 100%;
    margin:  auto;
    display: flex;
    justify-content: space-evenly;
    /* row-gap: 20px; */
}

.aboutMainDivImg{
    width: 40%;
    /* background-color: #d38888; */


}
.aboutMainDivText{
    width: 35%;
    text-align: center;
    margin: auto 0px;
    /* background-color: #c1c110; */
}
.aboutImg {
    width: 100%;
    height: 80vh;
    box-shadow: rgb(0, 0, 0) 0px 30px 90px;   
}
 .aboutTitle{
    font-size: 3rem;
    font-weight: 100;
    color: #c1c110; 
    /* margin-bottom: 20px; */
    
}
.aboutMain{
    font-family:Arial, Helvetica, sans-serif;
    /* font-weight: ; */
    font-size: 1.12rem;
    margin-top: 20px;
    word-spacing: 2px;
    /* max-height: 51rem; */
}


@media screen and (min-width:800px) and (max-width:1006px) {

    #about {

        width: 100%;
        height: auto;
        padding: 1rem;
    }
    .aboutImg {
        /* width:auto; */
        height: 60VH;
        box-shadow: rgb(0, 0, 0) 0px 30px 90px;   
        /* display: none; */
    }
    .aboutTitle{
        font-size: 2rem;
        font-weight: 100;
        color: #c1c110; 
        /* margin-bottom: 20px; */
        
    }
    .aboutMain{
        font-family:Arial, Helvetica, sans-serif;
        /* font-weight: ; */
        font-size: 1rem;
        margin-top: 20px;
        word-spacing: 1px;
        /* max-height: 51rem; */
    }
}

@media  (max-width:800px){
    
    .aboutTitle{
        font-size: 3rem;
        font-weight: 100;
        color: #c1c110; 
        /* margin-bottom: 20px; */
        
    }
    .aboutMain{
        font-family:Arial, Helvetica, sans-serif;
        /* font-weight: ; */
        font-size: 1.12rem;
        margin-top: 20px;
        word-spacing: 2px;
        /* max-height: 51rem; */
    
    }
    .aboutMainDiv{
        width: 100%;
        margin:  auto;
        display: flex;
        flex-direction: column-reverse;
        /* justify-content: space-evenly; */
        /* row-gap: 20px; */
    }
    .aboutMainDivText{
        width: 100%;
        text-align: center;
        margin: auto 0px;
        /* background-color: #c1c110; */
    }
    .aboutImg{
        display: none;
    }

}

@media (max-width:410px){
   .aboutMain{
    font-size: 1rem;
   }
}